      </a>
    </div>
    <div class="dapp">
      <img src="" class="dapp-logo" /><span class="text">MarketPlace</span>
      <button @click="dappReloc" class="dapp-reloc">
        View in CyberMiles App
      </button>
    </div>
    <div class="footer">
